.mbr-block{
  text-align: center;
  position: relative;
  &.grid3{
    .row{
      margin-left: -10px;
      margin-right: -10px;
    }
    .row>div{
      padding: 10px;
    }
    .row>div:nth-child(3n+1)>div{
      // margin-left: -10px;
    }
    .row>div:nth-child(3n+3)>div{
      // margin-right: -10px;
    }
  }
  .grid2{
    .row>div{
      padding: 10px;
    }
    .row>div:nth-child(2n+1)>div{
      margin-left: -10px;
    }
    .row>div:nth-child(2n+2)>div{
      margin-right: -10px;
    }
  }
  h2{
    text-transform: uppercase;
    font-size: 32px;
    line-height: 42px;
    margin-top: 25px;
    @media screen and (max-width: @mobile){
      font-size: 24px;
    }
  }
}

.mbr-block__pluses{

}

.mbr-block__game{
  padding: 90px 0 131px;
  @media screen and (max-width: @mobile){
    padding: 50px 0;
  }
  .question{
    margin-bottom: 46px;
    font-size: 19px;
    @media screen and (max-width: @mobile){
      display: none;
    }
  }
  .mbr-btn{
    margin: 0 10px;
    width: 100% / 5;
    @media screen and (max-width: @mobile){
      display: none;
    }

  }
  h2{
    margin-top: 0;
    margin-bottom: 12px;
    @media screen and (max-width: @mobile){
      padding: 0 50px;
      line-height: 29px;
    }
  }
}
.mbr-block__serves{
  // text-align: center;
  margin-bottom: 22px;
  .servese{
    border: 2px solid @yellow;
    padding: 45px 0 30px;
    height: 230px;
    transition: all 300ms;
    cursor: pointer;
    &:hover{
      background-color:  rgba(246, 191, 72, 0.5);
    }
    @media screen and (max-width: @mobile){
      padding: 5px 0 10px;
      height: 142px;
    }
  }
  .img{
    margin: auto;
    @media screen and (max-width: @mobile){
      transform-origin: center;
      transform: scale(0.7);
    }
  }
  h2{
    margin-bottom: 19px;
  }
  h3{
    position: absolute;
    bottom: 45px;
    margin: auto;
    left: 0;
    right: 0;
    text-transform: uppercase;
    line-height: 19px;
    @media screen and (max-width: @mobile){
      bottom: 30px;
      font-size: 14px;
    }
  }
  // h3{
  //   text-transform: uppercase;
  // }
  // .row>div{
  //   padding: 10px;
  // }
  // .row>div:nth-child(1)>div, .row>div:nth-child(4)>div{
  //   margin-left: -10px;
  // }
  // .row>div:nth-child(3)>div, .row>div:nth-child(6)>div{
  //   margin-right: -10px;
  // }
  .svg-sprite--tooth-white{
    position: relative;
    top: -13px;
  }
  .svg-sprite--teeth{
    position: relative;
    top: 15px;
  }
  @media screen and (max-width: @mobile){
    &.grid3 .row{
      margin-left: 5px;
      margin-right: 5px;
    }
    &.grid3 .row>div{
      padding: 5px;
      // margin: 5px;
    }
  }
}
.mbr-block__about{
  text-align: center;
  border: 2px solid @dark;
  padding: 60px 25% 120px;
  @media screen and (max-width: @mobile){
    margin-left: 10px;
    margin-right: 10px;
    padding: 36px 16px;
  }
  h2{
    margin-bottom: 47px;
    @media screen and (max-width: @mobile){
      margin-top: 0;
      line-height: 30px;
      margin-bottom: 36px;
    }
  }
  p{
    font-size: 16px;
    margin-bottom: 15px;
    line-height: 19px;
  }
  .mbr-backgrounds{
    z-index: -1;
    &>div{
      z-index: -1;
      position: absolute;
    }
    @media screen and (max-width: @mobile){
      display: none;
    }
  }
  .png-sprite--flowers{
    top: -37px;
    left: -33px;
  }
  .png-sprite--small-flower{
    bottom: 0;
    right: 0;
  }
}

.mbr-block__docs{
  margin-bottom: 43px;
  .row{
    margin-bottom: 61px;
  }
  .mbr-btn{
    width: ~"calc(33% - 20px)";
    @media screen and (max-width: @mobile){
      width: ~"calc(100% - 20px)";
      // margin: 0 10px;
    }
  }
  .img{
    width: 100%;
    height: auto;
    img{
      width: 100%;
      opacity: 0;
    }
  }
  .doc{
    background-color: @gray;
  }
  .name{
    color: @yellow;
    text-transform: uppercase;
    margin-top: 35px;
  }
  .special{
    color: @white;
    padding-bottom: 50px;
  }
  .img{
    background-image: url(../img/images/300.png);
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    // min-height: 305px;
  }
  hr{
    margin: 20px 28%;
  }
  h2{
    margin-top: 48px;
  }
  .btn-icon{
    top: -43px;
    left: 3px;
  }
}
.mbr-block__news{
  .row{
    text-align: left;
  }
}
.mbr-block__clients{
  margin-bottom: 90px;
  .info{
    background-color: @gray;
    color: @white;
    text-align: left;
    padding: 40px;
    @media screen and (max-width: @mobile){
      text-align: center;
      padding: 30px;
    }
  }
  .pic{
    background-image: url(../img/images/480x500.png);
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    @media screen and (max-width: @mobile){
      min-height: 400px;
    }
  }
  .name{
    text-transform: uppercase;
    font-size: 24px;
    margin-bottom: 25px;
    @media screen and (max-width: @mobile){
      font-size: 16px;
      margin-bottom: 11px;
    }
  }
  .prof{
    margin-bottom: 5px;
  }
  .site{
    color: @yellow;
    text-decoration: underline;
    margin-bottom: 60px;
  }
  .descr{
    margin-bottom: 160px;
    line-height: 19px;
    @media screen and (max-width: @mobile){
      display: none;
    }
  }
  .item{
    @media screen and (max-width: @mobile){
      margin: 0 10px;
    }
  }
  .item>div{
    height: 500px;
    @media screen and (max-width: @mobile){
      height: 200px;
    }
  }
  .mbr-btn{
    margin-top: 60px;
    width: ~"calc(33% - 20px)";
    @media screen and (max-width: @mobile){
      width: ~"calc(100% - 20px)";
    }
  }
  .btn-icon{
    left: -13px;
    top: -29px;
  }
}
.mbr-client-clider{
  position: relative;
  .owl-nav>div{
    position: absolute;
    color: @white;
    *{
      display: inline-block;
      vertical-align: top;
    }
    div{
      margin: 3px 16px;
    }
  }
  .owl-prev{
    width: 25%;
    height: 50px;
    bottom: 0;
    right: 25%;
    @media screen and (max-width: @mobile){
      width: 50%;
      right: initial;
      left: 0;
      text-align: left;
    }
  }
  .owl-next{
    width: 25%;
    height: 50px;
    bottom: 0;
    right: 0;
    @media screen and (max-width: @mobile){
      width: 50%;
      text-align: right;
    }
  }
}
.mbr-block__pluses{
  margin-bottom: 52px;
  .plus{
    height: 238px;
  }
  .img{
    margin: auto;
    position: relative;
  }
  .descr{
    position: absolute;
    bottom: 0;
    margin: auto;
    left: 0;
    right: 0;
    width: 100%;
    text-align: center;
    padding: 10%;
  }
  .svg-sprite--cups{
    top: 40px;
  }
  .svg-sprite--men{
    top: 30px;
  }
  .svg-sprite--house{
    top: 50px;
  }
  .svg-sprite--horse{
    top: 45px;
  }
}
.mbr-block__news{
  margin-bottom: 177px;
  @media screen and (max-width: @mobile){
    margin-bottom: 50px;
  }
  .mbr-news &{
    margin-bottom: 0px;
  }
  .mbr-news & .row>div{
    height: 450px;
  }
  h2{
    margin-bottom: 45px;
    .mbr-news &{
      margin-top: 60px;
      margin-bottom: 50px;
    }
  }
  img{
    width: 100%;
    opacity: 0;
  }
  .img{
    margin-bottom: 18px;
    // background-image: url(../img/images/300x200.png);
    // background-position: 50% 50%;
    // background-repeat: no-repeat;
    // background-size: cover;
    // height: 200px;
    overflow: hidden;
    .zooming{
      width: 100%;
      height: 100%;
      background-image: url(../img/images/300x200.png);
      background-position: 50% 50%;
      background-repeat: no-repeat;
      background-size: cover;
      transition: all 2s;
      &:hover{
        transform: scale(1.25);
      }
    }
  }
  .date{
    color: @light;
    margin-bottom: 18px;
  }
  .title{
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 12px;
  }
  .row{
    margin-bottom: 78px;
    @media screen and (max-width: @mobile){
      .grid3&{
        margin-left: 10px;
        margin-right: 10px;
      }
    }
    @media screen and (max-width: @mobile){
      &>div:not(:first-child){
        display: none;
      }
      .full&>div:not(:first-child){
        display: block;
      }
    }
  }
  .mbr-btn{
    width: ~"calc(33% - 20px)";
    @media screen and (max-width: @mobile){
      width: ~"calc(100% - 20px)";
    }
  }
  .btn-icon{
    top: initial;
    left: initial;
    right: 26px;
    bottom: -31px;
  }
}
.mbr-block__contact{
  margin-bottom: 20px;
  .ask{
    padding: 47px 80px 38px;
    border: 2px solid @dark;
    margin-right: 10px;
    @media screen and (max-width: @mobile){
      margin-left: 10px;
      padding: 36px 20px 50px;
    }
  }
  h2{
    margin-top: 0;
    margin-bottom: 30px;
  }
  .btn-icon{
    left: -10px;
    top: 5px;
  }
  .address{
    position: absolute;
    bottom: 0;
    padding: 20px 30px;
    background-color: @yellow;
    width: 100%;
  }
  .row>div:last-child{
    height: 100%;
  }
  #map{
    height: 100%;
  }
  .find{
    margin-left: 10px;
    position: relative;
    @media screen and (max-width: @mobile){
      display: none;
    }
  }
  .row{
    position: relative;
  }
}
.mbr-block--mobile-info{
  display: none;
  @media screen and (max-width: @mobile){
    display: block;
  }
  .phone{
    font-size: 20px;
    margin-top: 20px;
  }
  .address{
    margin: 20px 0;
    padding: 0 50px;
  }
  #howto-map-mobile{
    margin-bottom: 20px;
    text-decoration: underline;
  }
  img{
    width: 66px;
    height: 62px;
    margin-top: 30px;
  }
  .book{
    padding: 0 10px;
    // display: none;
  }
  #mbr-book-mobile{
    display: none;
  }
  .descr{
    margin-bottom: 20px;
  }
  .btn-icon{
    display: none;
  }
  #mbr-book-open-mobile{
    padding: 0;
    width: 66%;
    height: 32px;
  }
  .mbr-form{
    .mbr-btn{
      height: 32px;
      padding: 0;
      width: 66%;
    }
  }
}
@media screen and (max-width: @mobile){

#mbr-mobile-docs-slider{
  margin-left: 0;
  margin-right: 0;
  .owl-item>div{
    padding: 0 5px;
  }
  .owl-stage-outer{
    padding: 0;
  }
}


}




